import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import CreateQuize from './CreateQuize';
import Questions from './Questions';
import Choice from './Choice';
import {useState} from 'react';

function App() {
    const [flag, setflag] = useState(0);
    let addQuestions = (title) => {
        alert(title);
        setflag(1);
        console.log(flag);
    }
    let addChoices = () => {
        alert("hi");
        setflag(2);
        console.log(flag);
    }
    if (flag === 0) {
        return (
            <div className="container">
                <CreateQuize data={{addQuestions: addQuestions.bind(this)}}></CreateQuize>
            </div>
        );
    } else if (flag === 1) {
        return (
            <div className="container">
                <Questions data={{addChoices: addChoices.bind(this)}}></Questions>
            </div>
        );
    } else if (flag === 2) {
        return (
            <div className="container">
                <Choice></Choice>
            </div>
        );
    }

}

export default App;
